import React from 'react';
import { ListItem, Card, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { flexbox } from '@material-ui/system';

const cssStyles = makeStyles(theme => ({
    boxContainer: {
        backgroundColor: "red",
        flexDirection: "row",
        display: "flexbox",
    },
}));

function RenderItem(props) {
    const useStyle = cssStyles();
    return (
        <ListItem>


            <Card >
                <Box className={useStyle.box}>
                    <div id="cardImage">
                        item1
        <img src={props.project.tileTile.gif} width="200px" height="200px"></img>
                    </div>
                    <div id="cardContent">
                        <p>{props.project.tileTile.description}</p>
                        item2
    </div>
                </Box>
                {/* <pre>{JSON.stringify(props,null,2)}</pre> */}
                {/* <img src={props.project.tileTile.gif} alt="project gif"  width="250"/> */}

            </Card>

        </ListItem>
    );
}

export default RenderItem;