import React from 'react';
import { ListItem, Card, Box, Grid, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { flexbox } from '@material-ui/system';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';

const cssStyles = makeStyles(theme => ({
    boxContainer: {
        backgroundColor: "red",
        flexDirection: "row",
        display: "flexbox",
    },
    content: {
        padding: "10px"
    }
}));

function RenderItem(props) {
    const useStyle = cssStyles();
    return (
        <ListItem>
            <Card >
                <Grid container direction="row">
                    <Grid item md={3}>
                        <img src={props.project.tileTile.gif} width="200px" height="200px"></img>
                    </Grid>
                    <Grid item md={9}>
                        <Grid container direction="column" justify="space-between" wrap="wrap">
                            <Grid item className={useStyle.content}>
                                <p>{props.project.tileTile.description}</p>
                            </Grid>
                            <Grid container direction="row">
                                <Grid item>
                                    <Button>Source Code</Button>
                                </Grid>
                                <Grid item>
                                    <Button>Demo</Button>
                                </Grid>
                                <Grid item>
                                    <Button>
                                        <FavoriteBorderIcon></FavoriteBorderIcon>
                                    </Button>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Card>
        </ListItem>
    );
}

export default RenderItem;